<template>
    <div id="Software" class="container">
        <bread ></bread>
        <div class="row">
            <!-- <div id="left" class="col-md-4 col-xs-12">
                <ul class="left-container wow bounceInLeft">
                    <p>当前位置</p>
                    <li v-for="(item,index) in softwareList" :key="index">
                        <router-link :to=item.path>{{item.name}}</router-link>
                    </li>
                </ul>
            </div> -->
            <div id="right" class="col-md-12 col-xs-12  wow bounceInRight">
                  <el-table
                    :data="tableData.slice((currentPage-1)*pageSize,currentPage*pageSize)"
                    style="width: 100%"
                    max-height="700">
                    <el-table-column
                        fixed
                        prop="ID"
                        label="Object"
                        width="80">
                    </el-table-column>
                    <el-table-column
                        prop="projection"
                        label="Project category"
                        width="250">
                    </el-table-column>
                    <el-table-column
                        prop="name"
                        width="500"
                        label="The name of the project">
                    </el-table-column>
                    <el-table-column
                    prop="time"
                    width="120"
                    label="Start time">
                    </el-table-column>
                    <el-table-column
                    prop="people"
                    label="Participants">
                    </el-table-column>
                    </el-table>
                    <div>
                        <el-pagination id="page" align='center' @size-change="handleSizeChange" @current-change="handleCurrentChange" 
                        :current-page="currentPage" 
                        :page-sizes="[1,5,10,20]" 
                        :page-size="pageSize" 
                        layout="total, sizes, prev, pager, next, jumper" 
                        :total="tableData.length">
                        </el-pagination>
                    </div>
            </div>
        </div>
    </div>
    <!-- <div>
        <button @click="handleFileClick">Upload Word File</button>
        <div id="wordViewer"></div>
    </div> -->
</template>
<script>
import { WOW } from "wowjs";
import Bread from '../components/Bread.vue';
import { handleFileClick } from '../assets/js/mont';
import tableData from '../assets/data/project.json'

export default {
  components: { Bread },
    name: 'Software',
    data(){
        return{
            softwareList: [
               
            ],
            tableData,
            currentPage: 1, // 当前页码
            pageSize: 10// 每页的数据条数
        }
    },
    methods:{
        handleFileClick,
         //每页条数改变时触发 选择一页显示多少行
                handleSizeChange(val) {
                    console.log(`每页 ${val} 条`);
                    this.currentPage = 1;
                    this.pageSize = val;
                },
                //当前页改变时触发 跳转其他页
                handleCurrentChange(val) {
                    console.log(`当前页: ${val}`);
                    this.currentPage = val;
                }
    },
    computed:{
        total: function(){
            return this.tableData.length*this.pageSize + 1
        }
    },
    mounted() {
        var wow = new WOW();
        wow.init();
    },
}
</script>
<style scoped>
 >>> #page{
    margin: 0 auto;
    max-width: 100%;
    height: auto;
    display: grid; 
    grid-template-columns: 1fr 2fr 2fr 1fr 3fr 1fr 2fr 1fr; 
    grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
    justify-content: center; 
    align-content: center; 
    justify-items: center; 
    align-items: stretch;  
    
  }
#left{
    margin: 50px 0;
}
.left-container{
    width: 60%;
    margin: 0 auto;
    border: 1px solid #474747;
    border-radius: 5px;
}
.left-container>p{
    text-align: center;
    line-height: 45px;
    padding: 0;
    margin: 0;
    background: #474747;
    color: #fff;
    font-size: 18px;
    font-weight: bold;
}
.left-container>li {
  text-align: center;
  height: 38px;
  line-height: 38px;
  margin: 0;
  border-top: 1px solid #474747;
}
.left-container>li>a{
    text-decoration: none;
}
.left-container>li:hover{
    background: #928989;
}

#right{
    padding: 50px 0;
}
.block{
        margin: 0 auto;
    }
@media screen and (max-width: 768px){
    #right{
        padding: 15px;
    }

}

</style>




