// 获取用于展示 Word 文件内容的容器
import * as mammoth from 'mammoth/mammoth.browser.min.js'
const wordViewer = document.getElementById('wordViewer');
export function handleFileClick() {
  const fileInput = document.createElement('input');
  fileInput.type = 'file';
  fileInput.accept = '.doc,.docx';
  fileInput.addEventListener('change', handleFileInputChange);
  fileInput.click();
}

function handleFileInputChange(event) {
  const wordViewer = document.getElementById('wordViewer');
  const file = event.target.files[0];

  if (!file) {
    console.error("No Word file is selected.");
    return;
  }
  
  const reader = new FileReader();
  reader.onload = function(e) {
    const arrayBuffer = e.target.result;
    
    mammoth.extractRawText({ arrayBuffer: arrayBuffer })
      .then(function(result) {
        const html = result.value;
        wordViewer.innerHTML = html;
        
      })
      .catch(function(error) {
        console.error("Error parsing the Word file: ", error);
      });
  };

  reader.onerror = function(error) {
    console.error("Error reading the Word file: ", error);
  };

  reader.readAsArrayBuffer(file);
}


